// scss-lint:disable all

.pricing {
  padding: $spacer 0;
  border: 1px solid $gray-300;
  border-radius: $border-radius;
  text-align: center;

  &:hover {
    border: 1px solid theme-color("primary");
  }
}

.pricing-title {
  padding: $spacer * 1.5;
  font-size: 32px;
  color: $gray-800;
}

.pricing-price {
  font-size: 64px;
  display: inline-block;
  color: theme-color("primary");

  .currency {
    color: $gray-600;
  }
}

.pricing-desc {
  padding: $spacer * 2;
}

.button-container {
  text-align: center;
  position: relative;
  padding: 42px 0 45px 0;
  width: 100%;
  margin: 0;
  height: auto;
  background: none;
}

.pricing-time {
  font-weight: 300;
  letter-spacing: -1px;
  margin-top: 5px;
  font-size: 30px;
}
.iconita {
  line-height: 52px;
  font-size: 48px;
  height: 52px;
}



.pricing-option,
.pricing-desc {
  //text-transform: capitalize;
  text-align: center;
  line-height: 1.5;
  color: #666;
  font-family: PT Sans;
  font-size: 16px;
}
.pricing-option.not {
  opacity: .45;
  text-decoration: line-through;
}

.pricing-option.selected {
  background: #3595f8;
}

.pricing-row.selected .fa {
  font-size: 13px;
  top: 0;
}
.fa {
  color: theme-color("primary");
  font-size: 11px;
  margin-right: 10px;
  position: relative;
  top: -1px;
}

.currency {
  font-size: 34px;
  margin-right: 5px;
  position: relative;
  top: -20px;
}
.billing-time {
  color: #b3b3b3;
  font-size: 14px;
  line-height: 14px;
  margin-top: 8px;
}

.secondary-button {
  background: transparent;
  &:hover {
    background: #fff;
  }
  &.secondary-button-inverse:hover {
    background: theme-color("primary");
  }
}

.pricing.inverse {
  background: theme-color("light");
}
